.hands-container {
    position: relative;
    max-width: 238px;

    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 238px;
        height: 62px;
        top: 0;
        background-image: url(../../images/hands-icon.png);
        background-repeat: no-repeat;
        background-position: center;
    }

    &::after {
        content: '';
        display: block;
        position: absolute;
        width: 37px;
        height: 62px;
        top: 0;
        right: 0;
        background-image: url(../../images/hands-icon-right.png);
        background-repeat: no-repeat;
        background-position: bottom right;
        transform: translate3d(-50px, -15px, 0);
        animation: moveHand 500ms ease;
        animation-delay: 500ms;
        animation-fill-mode: forwards;
    }

    @keyframes moveHand {
        0% {
            transform: translate3d(-50px, -15px, 0);
        }

        100% {
            transform: translate3d(0, 0, 0);
        }
    }
}

.canvas {
    transition: opacity 1s ease;
}